.header {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  z-index: 1;
  left: 90%;
  width: 10%;
  height: 65%;
  position: fixed;
  background-color: rgba(0, 0, 0, 0);
}

.appname-container {
  display: flex;
  flex-direction: column;
}
.appname-container-mobile {
  position: absolute;
  z-index: 2;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
}
#collasible-nav-dropdown {
  margin: 0;
  padding-left: 130px;
  color: orange;
  font-family: "Montserrat", sans-serif;
  position: absolute;
  display: flex;
  flex-direction: column;
}

.app-name {
  margin: 10px;
  border-style: none;
  border-radius: 50px;
  background-color: rgb(82, 80, 80);
  padding: 15px;
  width: 50px;
  height: 50px;
}

.app-name:hover {
  transition: 400ms;
  border-style: none;
  border-radius: 50px;
  background-color: rgb(255, 180, 3);
  width: 100px;
  height: 50px;
  rotate: 180px;
}

.h-on-hover:hover {
  display: block;
  flex-direction: column;
  color: antiquewhite;
}
.h-on-hover {
  display: none;
}
